import React from 'react'


export const TodoItem = ({ todo, onDelete }) => {
    return (
        <>
        <div className="my-3">
            <h6>{todo.sno + 1}.</h6>
            <h4>{todo.title}</h4>
            <p>{todo.desc}</p>
            <button className="btn btn-danger btn-sm" onClick={() => { onDelete(todo) }}>Delete</button>
        </div>
        <hr/>
        </>
    )
}
