import logo from './logo.svg';
import './App.css';
import Header from "./MyComponents/Header";    // for rfc
import About from "./MyComponents/About";    // for rfc
import { AddTodo } from "./MyComponents/AddTodo";    // for rafc
import { Footer } from "./MyComponents/Footer"; // for rafc
import { Todos } from "./MyComponents/Todos"; // for rafc

import React, { useState, useEffect } from 'react';


import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

function App() {

  let initTodo;
  if (localStorage.getItem("todos") === null) {
    initTodo = []
  }
  else {
    initTodo = JSON.parse(localStorage.getItem("todos"));
  }

  const onDelete = (todo) => {
    console.log("I am deleting...", todo)

    setTodos(todos.filter((e) => {
      return e !== todo;
    }));
    localStorage.setItem("todos", JSON.stringify(todos));
  }


  const [todos, setTodos] = useState(initTodo);



  const addTodo = (title, desc) => {
    console.log("I am adding todo", title, desc)
    let sno;
    if (todos.length === 0) {
      sno = 0
    }
    else {
      sno = todos[todos.length - 1].sno + 1;

    }
    const MyTodo = {
      sno: sno,
      title: title,
      desc: desc
    }
    setTodos([...todos, MyTodo]);
    console.log(MyTodo)



  }


  useEffect(() => {

    localStorage.setItem("todos", JSON.stringify(todos));

  }, [todos])
  return (
    <>
      <Router>
        <Header title="My Todos List" searchBar={true} />

        <Switch>

          <Route exact path="/" render={() => {
            return (
              <>
                <AddTodo addTodo={addTodo} />
                <Todos todos={todos} onDelete={onDelete} />
              </>
            )
          }}>

          </Route>

          <Route exact path="/about">
            <About />
          </Route>

        </Switch>

        <Footer />

      </Router>
    </>
  );
}

export default App;
